.deposit-menu {
  padding: 1rem;
}
.deposit-menu h2 {
  font-family: 'OpenSansPX', sans-serif;
  font-size: 3rem;
  font-weight: normal;
  margin: 1.5rem 0 1rem;
}
.deposit-menu span {
  font-family: 'OpenSansPX', sans-serif;
  font-size: 2rem;
  font-weight: normal;
  color: #474747;
}
.deposit-menu input {
  padding: 0.5rem;
  font-family: 'OpenSansPX', sans-serif;
  font-size: 2rem;
  background-color: #848484;
  width: 100%;
  border: 4px inset #BFBFBF;
  text-align: right;
}
.deposit-menu input::placeholder {
  text-align: left;
}
.deposit-menu input::-webkit-outer-spin-button,
.deposit-menu input::-webkit-inner-spin-button {
  display: none;
}
.deposit-menu input:focus-visible {
  outline: none;
}
.deposit-menu .buttons-container {
  display: flex;
  flex-wrap: nowrap;
  flex-direction: row;
  justify-content: space-between;
  align-content: center;
  align-items: center;
  gap: 1rem;
  margin-bottom: 0.5rem;
}
.deposit-menu .buttons-container button {
  width: 10rem;
  padding: 0.75rem !important;
  height: 100%;
  outline: 2px solid #474747 !important;
}
.deposit-menu .input-wrapper {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  flex-grow: 1;
  background-color: #999999;
  padding: 4px;
  border: 2px solid #474747;
}
